.headerParentDiv {
    padding: 10px;
    background-color: rgb(201, 172, 45);
    position: fixed;
    width: 96.5vw;
    z-index: 999;
  }
  
  .headerChildDiv {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .placeSearch {
    width: 16em;
    height: 48px;
    display: flex;
    align-items: center;
    padding: 0 8px;
    border: 2px solid #010607;
    border-radius: 4px;
    background-color: rgb(227, 229, 233);
  }
  
  .placeSearch ::selection {
    border-color: #020303;
    outline-color: #080808;
  }
  
  .placeSearch input {
    border-color: transparent;
    outline-color: transparent;
  }
  
  .productSearch {
    width: 35em;
    height: 48px;
    border: 2px solid #002f34;
    border-radius: 4px;
    display: flex;
    align-items: center;
    background-color: rgb(3, 5, 3);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .productSearch .input {
    width: 100%;
    height: 100%;
    display: flex;
  }
  
  .productSearch .input input {
    border-color: transparent;
    outline-color: transparent;
    width: 100%;
    align-items: center;
  }
  
  .productSearch .searchAction {
    min-width: 48px;
    height: 48px;
    position: relative;
    display: flex;
    cursor: pointer;
    background-color: #002f34;
    border-radius: 0 4px 4px 0;
  }
  
  .productSearch .searchAction svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    fill: white;
  }
  
  .language {
    height: 48px;
    display: flex;
    align-items: center;
  }
  
  .language span {
    /* justify-content: space-between;
    align-items: center; */
    font-size: 15px;
    white-space: normal;
    text-overflow: ellipsis;
    font-weight: bolder;
    margin-right: 5px;
  }
  
  .language svg {
  }
  
  .span {
    text-decoration: none;
    font-size: 16px;
    white-space: normal;
    text-overflow: ellipsis;
    font-weight: bolder;
    margin-right: 5px;
  }
  
  .loginPage hr {
    margin: 0;
    background-color: black;
    height: 2px;
  }
  
  .loginPage a{
    text-decoration: none;
  }
  
  .sellMenu {
    position: relative;
    right: 50px;
    cursor: pointer;
  }
  
  .sellMenu .all {
    fill:whitesmoke
  }
  
  .sellMenu .top {
    fill: #1a070a;
  }
  
  .sellMenu .left {
    fill: #af0808;
  }
  
  .sellMenu .right {
    fill: #386de0;
  }
  .sellMenu .sellMenuContent {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.5px;
    color: #002f34;
  }
  .brandName {
      font-family:Georgia, 'Times New Roman', Times, serif;
  }

.span1{
    color: rgb(250, 250, 250);
}
  
  /* .sellMenu .sellMenuContent svg {
    align-items: center;
  } */
  .sellMenu .sellMenuContent span {
    margin-left: 5px;
  }

  .brandName:hover{
    cursor: pointer;
  }
  
  @media only screen and (max-width: 700px) {
    .placeSearch,
    .productSearch,
    .sellMenu {
      display: none;
    }
  }

  .filtersCategory{
    margin-left: 10px;
  }

  .searchBtn{
    background-color: royalblue;
  }
  .searchBtn:hover{
    cursor: pointer;
  }
